import deliveryDate from "../../../../json/deliverydate.json";

export default {
  /**
   * 新登場タグ、ランキングタグ、納車日 付与
   * @param {Array} data
   * @return {Array}
   */
  addStatus: (data) => {
    const deliveryDateMaster = deliveryDate.data;
    return data.map((d) => {
      // 納車日
      if (deliveryDateMaster) {
        const dateVal = deliveryDateMaster.find((e) => {
          return d.car_model_code === e.car_model_code;
        });
        // レクサス車で納期情報無しの場合=>"未定"
        d.deliveryDateComment = dateVal && dateVal.deliveryDateComment ? dateVal.deliveryDateComment : d.brand !== "トヨタ" ? "未定" : "";
      }

      d.imageTag = [];
      // 新登場マーク
      if (d.new_product === "1") {
        d.imageTag.push("new");
      }
      // ランキング
      if (d.ranking_now !== "") {
        d.imageTag.push("rank" + d.ranking_now);
      }
      return d;
    });
  },

  /**
   * 納期データ最終更新日
   * @return {String}
   */
  lastUpdate: () => {
    return deliveryDate.lastUpdate || "";
  },
};
