<template>
  <div v-if="!loaded" class="loading">データを読み込み中です</div>

  <div v-else-if="!cardata" class="message">
    データを読み込むことができませんでした。
  </div>

  <div v-else-if="!cardataResult.length" class="message">
    該当車種はありません。
  </div>

  <div v-else>
    <ResultCarList
      :cardata="cardata"
      :priceType="priceType"
      :bonus="bonus"
      :condition="condition"
      :brandType="brandType"
      :salesType="salesType"
      @alert="showAlertSales"
    ></ResultCarList>

    <transition name="fade">
      <!-- 販売店限定車種用 アラートメッセージ(モーダルウィンドウ) -->
      <AlertSales
          v-if="showAlertWin"
          :linkUrl="simulateUrl"
          @close="closeAlertSales"
        >
      </AlertSales>
    </transition>
  </div>
</template>

<script>
import lineupMaster from "../../../../json/cardata.json";
import salesLineupMaster from "../../../../json/salescardata.json";

import ResultCarList from "../components/ResultCarList";
import mixinPriceType from "../mixins/selectPriceType";
import lineupResultDealer from "../mixins/lineupResultDealer";

import AlertSales from "./AlertSales";

import filter from "../tools/filter";
import sort from "../tools/sort";
import listup from "../tools/listup";
import util from "../tools/utility";
import deliverydate from "../tools/deliverydate";

export default {
  mixins: [mixinPriceType, lineupResultDealer],
  props: ["condition", "brandType", "salesType", "exclusion"],
  components: {
    ResultCarList,
    AlertSales,
  },
  data() {
    return {
      lineupMaster: this.salesType !== "dealer" ? lineupMaster : salesLineupMaster,
      cardataOrigin: [],
      loaded: false,
      showAlertWin: false,
      simulateUrl: "",
    };
  },
  created() {
    this.formatData();
  },
  computed: {
    priceType() {
      // プラン/ボーナス カラム名
      const plan = "year" + this.term;
      const bonusMonth = "bonus" + this.bonus + "m";
      return [plan, bonusMonth, "taxin"].join("_");
    },
    cardataWithPlan() {
      // プラン/ボーナスでのフィルタ結果
      return this.sortCarData(this.cardataOrigin, this.priceType);
    },
    cardataResult() {
      // 検索結果
      const data = this.cardataWithPlan;
      return this.condition ? this.categorySearch(this.condition) : data;
    },
    cardata() {
      const data = this.cardataResult;
      // 件数をemit
      this.$emit("resultNum", data.length);
      // 表示用データ
      return this.condition
        ? data
        : this.salesType !== "dealer"
        ? this.listupGroup(data)
        : this.listupDealerSales(data);
    },
  },
  methods: {
    formatData() {
      // clone
      const data = JSON.parse(JSON.stringify(this.lineupMaster));

      // 任意の車種を除外
      let carData;
      carData = this.filterRemoval(data, this.exclusion);
      // 表示フラグでフィルタ
      carData = filter.viewFlag(carData);
      // brandType(url)でフィルタ
      carData = filter.brandType(carData, this.brandType);
      // 納期情報,Newステータスを追加
      carData = deliverydate.addStatus(carData);

      // 表示用Originalデータを格納
      this.cardataOrigin = carData;
      // プラン/ボーナス絞り込みボタンの表示フラグ用に対象件数をemit
      this.$emit("dataNum", this.cardataOrigin.length);

      /*
        (-> 価格タイプを設定[デフォルト値])
        -> 設定した価格タイプでフィルタ+ソート
        -> クルマタイプでグルーピング
        -> 表示用データを格納
        ここまでがcomputed(自動)で走る
      */

      // loading非表示
      this.loaded = true;
    },
    filterRemoval(data, condition) {
      // マイナス検索
      return filter.filter(data, condition, true);
    },
    sortCarData(data, priceType) {
      let res;
      // 価格順にソート
      res = sort.price(data, priceType);
      // brand名でソート
      res = sort.brand(res);
      return res;
    },
    listupGroup(data) {
      // カテゴリ > ブランドでグルーピング
      return listup.group(data, this.brandType);
    },
    clearCondition() {
      // 検索条件をクリア
      return this.$emit("clear");
    },
    categorySearch(val) {
      // 指定条件で検索
      const data =
        val.category === "price"
          ? this.searchWithPrice(val.option)
          : this.searchWithTag(val.option);

      if (val.category === "ranking") {
        // ランキング用にフォーマット
        return this.formatForRanking(data, val.option.status);
      } else if (val.category === "use") {
        // 追加コメント
        return util.addExComment(data, val.option);
      } else {
        return data;
      }
    },
    searchWithPrice(val) {
      // 予算で探す
      const minmax = val.status.split("-");
      const min = minmax[0];
      const max = minmax[1] !== "over" ? minmax[1] : 9999999;
      const cdt = [this.priceType, min, max];
      return filter.between(this.cardataWithPlan, cdt, true);
    },
    searchWithTag(val) {
      // タグで探す
      // (該当カラムに値が入っていないデータを除く)
      const cdt = [];
      if (Array.isArray(val)) {
        val.map((d) => {
          const _cdt = {};
          _cdt[d.status] = "";
          cdt.push(_cdt);
        });
      } else {
        const _cdt = {};
        _cdt[val.status] = "";
        cdt.push(_cdt);
      }
      return this.filterRemoval(this.cardataWithPlan, cdt);
    },
    formatForRanking(data, option) {
      // ランキング順でソート
      const _data = sort.ranking(data, option);
      // [rankingTag]カラムを挿入
      return util.addRankingTag(_data, option);
    },

    showAlertSales(url) {
      // 販売店限定車種をクリック => モーダル表示
      this.showAlertWin = true;
      this.simulateUrl = url;
    },
    closeAlertSales() {
      // モーダル内で「いいえ」をクリック => モーダル非表示
      this.showAlertWin = false;
      this.simulateUrl = "";
      }
  },
};
</script>

<style lang="scss" scoped>
// ローディング
.loading {
  padding: 2.4rem 0;
  min-height: 64px;
  background: no-repeat center center
    url("/assets/img/modules/carList/loading.gif");
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

// メッセージ
.message {
  padding: 4rem 0;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}
</style>
