exports.push([module.id, "@charset \"UTF-8\";\n/*\n* 変数管理用ファイル\n* 色、横幅、マージンなどの数値をここで管理\n*/\n/*\n例：w-mainの場合\nPCは左右にSP同様に12pxの余白をもたせ、\nwidthを100%\nmax-widthを984 + 14*2 = 1012とし、\n横幅を可変した際もレイアウト崩れのないようにする\n他の横幅変数についても同様　※FULLレイアウトは除く\n*/\n/* サイトカラー\n--------------------------------- */\n[data-type=\"_condition\"][data-v-0f1f5ed4] {\n  -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n}\n[data-type=\"_condition\"] li[data-v-0f1f5ed4] {\n    width: calc(33.3% - ((16px * 2) / 3));\n}\n[data-type=\"_condition\"] li[data-v-0f1f5ed4]:nth-child(1) {\n      margin-right: 50%;\n}\n[data-type=\"_condition\"] li[data-v-0f1f5ed4]:nth-child(n + 2) {\n      margin-top: 12px;\n}\n@media screen and (min-width: 835px) {\n[data-type=\"_condition\"] li[data-v-0f1f5ed4] {\n        width: calc(25% - ((16px * 3) / 4));\n}\n[data-type=\"_condition\"] li[data-v-0f1f5ed4]:nth-child(1) {\n          margin-right: 16px;\n}\n[data-type=\"_condition\"] li[data-v-0f1f5ed4]:nth-child(n + 2) {\n          margin-top: 0;\n}\n}\n", "",{"version":3,"sources":["/Users/yusuke.ikeda/works_dealer/kinto-jp.com-frontend-dealer/src/javascripts/contents/lineup/components/SelectorBonus.vue","/Users/yusuke.ikeda/works_dealer/kinto-jp.com-frontend-dealer/src/style/foundation/_variable.scss","/Users/yusuke.ikeda/works_dealer/kinto-jp.com-frontend-dealer/src/style/contents/lineup/_foundation.scss"],"names":[],"mappings":"AAAA,gBAAgB;ACAhB;;;CDIC;ACUD;;;;;;;CDFC;AC4CD;mCD1CmC;AACnC;EAgDE,mBAAe;MAAf,eAAe;AAAA;AA9Cf;IAiDE,qCAAqC;AAAA;AA/CrC;MAkDE,iBAAiB;AAAA;AAhDnB;MAmDE,gBAAgB;AAAA;AEjEpB;AFiBI;QAoDA,mCAAmC;AAAA;AAlDjC;UAqDA,kBAAkB;AAAA;AAnDlB;UAsDA,aAAa;AAAA;AACd","file":"SelectorBonus.vue","sourcesContent":["@charset \"UTF-8\";\n/*\n* 変数管理用ファイル\n* 色、横幅、マージンなどの数値をここで管理\n*/\n/*\n例：w-mainの場合\nPCは左右にSP同様に12pxの余白をもたせ、\nwidthを100%\nmax-widthを984 + 14*2 = 1012とし、\n横幅を可変した際もレイアウト崩れのないようにする\n他の横幅変数についても同様　※FULLレイアウトは除く\n*/\n/* サイトカラー\n--------------------------------- */\n[data-type=\"_condition\"] {\n  flex-wrap: wrap; }\n  [data-type=\"_condition\"] li {\n    width: calc(33.3% - ((16px * 2) / 3)); }\n    [data-type=\"_condition\"] li:nth-child(1) {\n      margin-right: 50%; }\n    [data-type=\"_condition\"] li:nth-child(n + 2) {\n      margin-top: 12px; }\n    @media screen and (min-width: 835px) {\n      [data-type=\"_condition\"] li {\n        width: calc(25% - ((16px * 3) / 4)); }\n        [data-type=\"_condition\"] li:nth-child(1) {\n          margin-right: 16px; }\n        [data-type=\"_condition\"] li:nth-child(n + 2) {\n          margin-top: 0; } }\n","/*\n* 変数管理用ファイル\n* 色、横幅、マージンなどの数値をここで管理\n*/\n\n\n// breakPoint\n$breakpoints: ('sm': 'screen and (min-width: 320px)',\n  'md': 'screen and (min-width: 835px)',\n  'xl': 'screen and (min-width: 1001px)',\n) !default;\n\n\n//width\n/*\n例：w-mainの場合\nPCは左右にSP同様に12pxの余白をもたせ、\nwidthを100%\nmax-widthを984 + 14*2 = 1012とし、\n横幅を可変した際もレイアウト崩れのないようにする\n他の横幅変数についても同様　※FULLレイアウトは除く\n*/\n\n$w-full : 100%;\n$w-main : 1008px; // ≒ 984px\n$w-mid : 828px; // ≒ 816px\n$w-small : 702px; // ≒ 690px\n\n$w-padding: 0 12px;\n\n// margin\n$mgn-xl : 60px; // section系(PC)\n$mgn-l : 48px; // section内 コンテンツ同士のmg(PC)\n$mgn-m : 36px; // section内でのコンテンツ要素同士のmg(PC) 見出しのmg(PC)\n$mgn-s : 24px; // 要素感の合間 ->ex)テキストの下にボタンなどの余白(PC/SP共通)\n$mgn-ss : 12px; // 注釈などちょっとしたmg(PC/SP共通)\n\n//line-height\n$text-height : 1.75;\n$title-height : 1.5;\n\n// テキストサイズ\n$txt_size_base: 1.4rem;\n// セッション見出しレベル\n$txt_size_heading01_pc: 4rem;\n$txt_size_heading01_sp: 3rem;\n// コンテンツ見出しレベル\n$txt_size_heading02_pc: 2.8rem;\n$txt_size_heading02_sp: 2.4rem;\n// コンテンツ内見出しレベル\n$txt_size_heading03_pc: 2rem;\n$txt_size_heading03_sp: 1.8rem;\n// ちょっと強調レベル\n$txt_size_heading04_pc: 1.6rem;\n$txt_size_heading04_sp: 1.6rem;\n\n/* サイトカラー\n--------------------------------- */\n\n//プライマリーカラーとその系統色\n$col_primary : #00708d;\n$col_primary2: #69A7BF;\n$col_primary3: #D9F7FF;\n$col_primary4: #7ECBC3;\n\n// グレースケール\n$col_font_base: #333;\n$white : #fff;\n$gray1: #6B6B6B;\n$gray2: #aaa;\n$gray3: #ccc;\n$gray4: #eaeaea;\n$gray5: #F7F7F7;\n\n// アクセントカラー / エラーカラー\n$col_accent_red: #E53935;\n$col_accent_red_light: #FF6759;\n$col_accent_red_light2: #FFA58F;\n$col_accent_pink: #FFEBEE;\n$col_accent_yellow_dark: #FDD835;\n$col_accent_yellow: #FFEE58;\n$col_accent_yellow_light: #FFF59D;\n$col_accent_yellow_light2: #FFFDE7;\n$col_accent_orange: #EB9500;\n\n// リンクカラー\n$col_link: #009DC5;\n$col_link_hover: #009DC5;\n$opacity_hover: .8;\n\n\n// 背景、ボーダー使用色\n$col_border : $gray4; // eaeaea\n$bggray : #e7e7e7;\n$bglightgray : $gray5; // F7F7F7\n$bgprimary: #e3eff2;\n\n//font-family\n$title : \"Noto Sans JP\", sans-serif;\n$text : '\"游ゴシック体\", \"游ゴシック Medium\", \"Yu Gothic Medium\",\"ヒラギノ角ゴ ProN W3\", \"Hiragino Kaku Gothic ProN\", \"Hiragino Sans\",\"ヒラギノ角ゴ W3\", \"メイリオ\", Meiryo, sans-serif';\n","@import \"../../foundation/variable\";\n\n$col_text: $col_font_base;\n$col_text_cap: #909090;\n$col_active: $col_primary;\n\n@mixin mq() {\n  @media screen and (min-width: 835px) {\n    @content;\n  }\n}\n"]}]);
